import React, { useState } from 'react';
import '../../styles/home/FAQ.scss';
import { dataFAQ } from '../../api/Api';
import { useTranslation } from 'react-i18next';
import ChevronImg from '../../assets/images/chevron.svg';
import { getAnswer, getQuestion } from '../../languages/language';

const FAQ = () => {

    const [activeIndex, setActiveIndex] = useState(null);

    // i18next

    const { t } = useTranslation();

    return (
        <div className='FAQ parent'>
            <div className="wrapper gap-2">
                <h1 className="sub-title">{t("faq")}</h1>
                <div className="cards gap-1">
                    {dataFAQ?.map((item) => (
                        <div key={item.id} className="question-bar round-07 pd-1-5">
                            <div className="question gap-2">
                                <h4 className="big-text">{getQuestion(item)}</h4>
                                <div className={`icon ${activeIndex == dataFAQ.indexOf(item) && "active-icon"}`} onClick={() => activeIndex != dataFAQ.indexOf(item) ? setActiveIndex(dataFAQ.indexOf(item)) : setActiveIndex(null)}>
                                    <img src={ChevronImg} alt="icn" className="icn-img" />
                                </div>
                            </div>
                            <p className={`min-text answer ${activeIndex == dataFAQ.indexOf(item) && "active-answer"}`}>{getAnswer(item)}</p>
                        </div>
                    ))}
                </div>
            </div>
        </div>
    );
};

export default FAQ;