import React from 'react';
import "swiper/css";
import { Autoplay } from "swiper";
import '../../styles/home/Comments.scss';
import { dataComments } from '../../api/Api';
import { useTranslation } from 'react-i18next';
import { Swiper, SwiperSlide } from "swiper/react";
import { getComment, getCustomer, getKorxona } from '../../languages/language';

const Comments = () => {

    // i18next

    const { t } = useTranslation();

    return (
        <div className='Comments parent'>
            <div className="wrapper gap-1">
                <h1 className="sub-title">{t('comment_title')}</h1>
                <p className="text comment-text">{t('comment_txt')}</p>
            </div>
            <Swiper
                spaceBetween={10}
                slidesPerView={1}
                autoplay={{
                    delay: 2500,
                    disableOnInteraction: false,
                }}
                loop={true}
                centeredSlides={true}
                breakpoints={{
                    450: {
                        slidesPerView: 1.5,
                        spaceBetween: 20,
                    },
                    768: {
                        slidesPerView: 2,
                        spaceBetween: 20,
                    },
                    1000: {
                        slidesPerView: 3,
                        spaceBetween: 30,
                    },
                }}
                modules={[Autoplay]}
                className="mySwiper"
            >
                {dataComments.map((item) => (
                    <SwiperSlide key={item.id} className="comment gap-1-5 round-1 pd-2">
                        <p className="text desc">{getComment(item)}</p>
                        <div className="bottom gap-1">
                            <img src={item.image} alt="img" className="img" />
                            <div className="texts gap-05">
                                <p className="big-text">{getCustomer(item)}</p>
                                <p className="text">{getKorxona(item)}</p>
                            </div>
                        </div>
                    </SwiperSlide>
                ))}
            </Swiper>
        </div>
    );
};

export default Comments;