import React, { Suspense, useRef, useState } from 'react';
import { store } from './redux/store';
import { Provider } from 'react-redux';
import HomeRouter from './routes/HomeRouter';
import RootLayout from './routes/RootLayout';
import Courses from './components/courses/Courses';
import Payment from './components/payments/Payment';
import FreeCourses from './components/freecourses/FreeCourses';
import Congratulations from './components/payments/Congratulations';
import { createBrowserRouter, createRoutesFromElements, Route, RouterProvider } from 'react-router-dom';

const App = () => {

  // modal options

  const [showModal, setShowModal] = useState(false);

  function changeModal() {
    setShowModal(!showModal)
  };

  // form-modal options

  const [showFormModal, setShowFormModal] = useState(false);

  function changeFormModal() {
    setShowFormModal(!showFormModal)
  };

  // navbar scrolling page options

  const faqPage = useRef();
  const coursesPage = useRef();
  const commentsPage = useRef();
  const teachersPage = useRef();
  const advantagesPage = useRef();

  function changeScrollFaq() {
    setTimeout(() => {
      window.scrollTo(0, faqPage.current.offsetTop)
    }, 100);
  };

  function changeScrollComments() {
    setTimeout(() => {
      window.scrollTo(0, commentsPage.current.offsetTop)
    }, 100);
  };

  function changeScrollAdvantage() {
    setTimeout(() => {
      window.scrollTo(0, advantagesPage.current.offsetTop)
    }, 100);
  };

  function changeScrollTeachers() {
    setTimeout(() => {
      window.scrollTo(0, teachersPage.current.offsetTop)
    }, 100);
  };

  function changeScrollCourses() {
    setTimeout(() => {
      window.scrollTo(0, coursesPage.current.offsetTop)
    }, 100);
  };

  // router options

  const router = createBrowserRouter(
    createRoutesFromElements(
      <Route path='/' element={<RootLayout {...{ showModal, showFormModal, changeModal, changeFormModal, changeScrollFaq, changeScrollComments, changeScrollAdvantage, changeScrollTeachers, changeScrollCourses }} />}>
        <Route index element={<HomeRouter {...{ faqPage, coursesPage, commentsPage, teachersPage, advantagesPage, changeModal }} />} />
        <Route path='courses/:id' element={<Courses {...{ changeFormModal }} />} />
        <Route path='free-courses/:id' element={<FreeCourses />} />
        <Route path='payment/:id' element={<Payment />} />
        <Route path='congrate/click' element={<Congratulations />} />
        <Route path='congrate/payme' element={<Congratulations />} />
        <Route path='congrate/success' element={<Congratulations />} />
        <Route path='congrate/uzum' element={<Congratulations />} />
      </Route>
    )
  )

  return (
    <Suspense fallback={<div>LOADING...</div>}>
      <Provider store={store}>
        <RouterProvider router={router} />
      </Provider>
    </Suspense>
  );
};

export default App;
