import React, { useState } from 'react';
import axios from 'axios';
import '../../styles/payments/Payment.scss';
import { useTranslation } from 'react-i18next';
import 'react-toastify/dist/ReactToastify.css';
import { CheckedIcon } from '../../assets/svgIcons';
import UzumLogo from '../../assets/images/uzum.webp';
import ClickLogo from '../../assets/images/click.png';
import PaymeLogo from '../../assets/images/payme.webp';
import { toast, ToastContainer } from 'react-toastify';
import { useDispatch, useSelector } from 'react-redux';
import { useNavigate, useParams } from 'react-router-dom';
import { changeAmount } from '../../redux/actions/Actions';

const Payment = () => {

    // router options

    const { id } = useParams();
    const navigate = useNavigate();

    // courses prices

    const price1 = 997000 //397000;
    const price2 = 997000 //697000;

    // redux options

    const dispatch = useDispatch();
    const [courseId, setCourseId] = useState(1);
    let selectedAmount = useSelector((state) => state.selectedAmount.amount);

    function changeAmountValue(item) {
        setCourseId(item);

        if (id == 1) {
            dispatch(changeAmount(item ? item == 6 ? price2 : price1 : 0));
        } else if (id == 2) {
            dispatch(changeAmount(item ? item == 5 ? price2 : price1 : 0));
        }
    };

    // session-storage options

    let jinsi = sessionStorage.getItem("user-jinsi");
    let userName = sessionStorage.getItem("user-name");
    let phoneValue = sessionStorage.getItem("user-phone");

    // form options

    const [isLoading, setIsloading] = useState(false);
    const [paymentType, setPaymentType] = useState('click');

    // payment options

    function startPayment(e) {
        e.preventDefault();
        setIsloading(true);

        const userData = new FormData();
        userData.append('gender', jinsi);
        userData.append('name', userName);
        userData.append('phone', phoneValue);

        // ----------to'lovga tayyorgarlik---------- //

        axios.post(`https://api.dargah.uz/backend/user/auth`, userData)
            .then(function (response) {
                if (response.data.user_id) {

                    const formData = new FormData();
                    formData.append('course_id', courseId);
                    formData.append('service', paymentType);
                    formData.append('return_url', `https://dargah.uz/congrate/success?`);
                    formData.append('user_id', response.data.user_id);

                    axios.post(`https://api.dargah.uz/backend/transaction/initialize`, formData)
                        .then(function (res) {
                            if (res.data.token) {
                                setIsloading(false);
                                setTimeout(() => {
                                    window.location.href = res.data.redirect_url;
                                }, 500);
                                navigate(`/congrate/success?${res.data.token}`);
                                localStorage.setItem("course-token", res.data.token)
                            }
                        })
                        .catch(function (error) {
                            console.log(error);
                            setIsloading(false);
                            toast.error("Qandaydir xatolik yuz berdi, qaytadan urinib ko'ring!", {
                                position: "top-right",
                                autoClose: 5000,
                                hideProgressBar: false,
                                closeOnClick: true,
                                pauseOnHover: true,
                                draggable: true,
                                progress: undefined,
                                theme: "light",
                            });
                        });
                }
            })
            .catch(function (error) {
                console.log(error);
            });
    };

    // i18next

    const { t } = useTranslation();
    let lang = localStorage.getItem('i18nextLng');

    return (
        <>
            <ToastContainer
                position="top-right"
                autoClose={5000}
                hideProgressBar={false}
                newestOnTop={false}
                closeOnClick
                rtl={false}
                pauseOnFocusLoss
                draggable
                pauseOnHover
                theme="light"
            />
            <div className='Payment parent'>
                <div className="wrapper gap-2">
                    <h1 className="title t-center mulish-700">{t("pay_title")}</h1>
                    {/* <h3 className="name mulish-600">{t("hoziroq")}</h3> */}
                    {lang == 'uz' ?
                        <h3 className="name mulish-600">Agar hoziroq kursimizni sotib olsangiz sizga ajoyib chegirma qilib beramiz. Ya’ni:</h3>
                        :
                        <h3 className="name mulish-600">Агар ҳозироқ курсимизни сотиб олсангиз сизга ажойиб чегирма қилиб берамиз. Яъни:</h3>
                    }
                    <h3 className="name mulish-600">{lang == 'uz' ? "«Soadatli oila» 997,000 so'm" : "«Соадатли оила» 997,000 сўм"}</h3>
                    <p className="text">{t("bunaqa")}</p>
                    <div className="payment-bar mtop-1-5">
                        <div className="left gap-1 pd-1-5 round-1">
                            <button className="price-card round-07 pd-1 gap-1" onClick={() => changeAmountValue(1)}>
                                <div className={`checked ${[1, 3, 5, 6].includes(courseId) && "active-checked"}`}>
                                    <div className="icon"><CheckedIcon /></div>
                                </div>
                                <p className="big-text">{lang == 'uz' ? "«Soadatli oila» 997 000 so'm" : "«Соадатли оила» 997 000 сўм"}</p>
                            </button>
                            {lang == "uz" ?
                                <div className="result-price mtop-1">
                                    <h1 className="name mulish-700">Jami:</h1>
                                    <h1 className="sub-title mulish-700">997 000 so'm</h1>
                                </div>
                                :
                                <div className="result-price mtop-1">
                                    <h1 className="name mulish-700">Жами:</h1>
                                    <h1 className="sub-title mulish-700">997 000 сўм</h1>
                                </div>
                            }
                        </div>
                        <div className="right gap-1 round-1 pd-1-5">
                            {lang == "uz" ?
                                <p className="text">To'lov turini tanlang</p>
                                :
                                <p className="text">Тўлов турини танланг</p>
                            }
                            <div className="payment-types gap-1">
                                <div className="type-card">
                                    <input type="radio" name="radio" id="click" checked={paymentType == "click"} onChange={() => setPaymentType("click")} />
                                    <label htmlFor="click" className='type-label round-1 pd-1'>
                                        <img src={ClickLogo} alt="click" className="logo" />
                                    </label>
                                </div>
                                <div className="type-card">
                                    <input type="radio" name="radio" id="payme" checked={paymentType == "payme"} onChange={() => setPaymentType("payme")} />
                                    <label htmlFor="payme" className='type-label round-1 pd-1'>
                                        <img src={PaymeLogo} alt="payme" className="logo" />
                                    </label>
                                </div>
                                <div className="type-card">
                                    <input type="radio" name="radio" id="uzum" checked={paymentType == "uzum"} onChange={() => setPaymentType("uzum")} />
                                    <label htmlFor="uzum" className='type-label round-1 pd-1'>
                                        <img src={UzumLogo} alt="uzum" className="logo" />
                                    </label>
                                </div>
                            </div>
                            <form onSubmit={startPayment} className="forms gap-05 mtop-1" aria-disabled>
                                <button type="submit" className={`pay-btn text round-05 pd-07 mtop-1-5 ${isLoading ? "loading-btn" : "op-08"}`} disabled={isLoading}>{t("payment_btn")}</button>
                            </form>
                        </div>
                    </div>
                </div >
            </div >
        </>
    );
};

export default Payment;