import React from 'react';
import "swiper/css";
import "swiper/css/navigation";
import "swiper/css/pagination";
import '../../styles/home/Header.scss';
import { Link } from 'react-router-dom';
import { dataCourses } from '../../api/Api';
import { useTranslation } from 'react-i18next';
import { Navigation, Pagination } from "swiper";
import Icon1 from '../../assets/images/icon1.svg';
import Icon2 from '../../assets/images/icon2.svg';
import Icon3 from '../../assets/images/icon3.svg';
import { Swiper, SwiperSlide } from "swiper/react";
import { RightArrowIcon } from '../../assets/svgIcons';
import { getDescription, getName } from '../../languages/language';

const Header = () => {

    // i18next 

    const { t } = useTranslation();

    return (
        <div className='Header parent'>
            <div className="texture"></div>
            <div className="texture1"></div>
            <div className="texture2"></div>
            <div className="wrapper">
                <Swiper navigation={true} loop={true} modules={[Navigation, Pagination]} className="mySwiper">
                    {dataCourses.map((item) => (
                        <SwiperSlide key={item.id} className="course pd-2">
                            <div className="top">
                                <div className="left gap-3">
                                    <h1 className="sub-title">{getName(item)}</h1>
                                    <p className="text">{getDescription(item)}</p>
                                    <div className="links" onClick={() => { window.ym(92266018, 'reachGoal', 'oila-kursi') }}>
                                        {item.disabled ?
                                            <div className="explore pd-1 big-text round-05 gap-1">
                                                {t("head_btn_1")}
                                            </div>
                                            :
                                            <Link to={`courses/${item.id}`} className="explore pd-1 big-text round-05 scale-05 gap-1">
                                                {t("head_btn")}
                                                <div className="arrow-icon"><RightArrowIcon /></div>
                                            </Link>
                                        }
                                    </div>
                                </div>
                                <div className="right">
                                    <img src={item.image} alt="img" className='img' />
                                </div>
                            </div>
                        </SwiperSlide>
                    ))}
                </Swiper>
                <div className="bottom gap-1 pd-2 round-1">
                    <div className="tool gap-1">
                        <div className="icon round-07">
                            <img src={Icon1} className='ic-img' />
                        </div>
                        <div className="text">{t("head1")}</div>
                    </div>
                    <div className="tool gap-1">
                        <div className="icon round-07">
                            <img src={Icon2} className='ic-img' />
                        </div>
                        <div className="text">{t("head2")}</div>
                    </div>
                    <div className="tool gap-1">
                        <div className="icon round-07">
                            <img src={Icon3} className='ic-img' />
                        </div>
                        <div className="text">{t("head3")}</div>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default Header;