import React, { useState } from 'react';
import '../styles/Footer.scss';
import { Link } from 'react-router-dom';
import Logo from '../assets/images/logo1.webp';
import { useTranslation } from 'react-i18next';

const Footer = ({ changeModal, changeScrollFaq, changeScrollComments, changeScrollTeachers, changeScrollCourses }) => {

    // i18next

    const { t } = useTranslation();

    // form options

    const [emailValue, setEmailValue] = useState("");

    let bot = {
        TOKEN: "5886103475:AAGeKpLQbe4cSoda09-pXDfGObdVgrnhlbs",
        chatID: "-1001551810849",
        message: `Emaili: ${emailValue}`
    };

    function sendEmail(e) {
        e.preventDefault();
        fetch(`https://api.telegram.org/bot${bot.TOKEN}/sendMessage?chat_id=${bot.chatID}&text=${bot.message}`, {
            method: "GET"
        })
            .then(success => {
                console.log('send message')
            }, error => {
                console.log(error)
            })

        changeModal();
        setEmailValue("");
    };

    return (
        <div className='Footer parent'>
            <div className="wrapper gap-4">
                <div className="top">
                    <Link to="/" onClick={() => window.scrollTo(0, 0)} className="logo">
                        <img src={Logo} alt="logo" className="logo-img" />
                    </Link>
                    <form onSubmit={sendEmail} className="forms">
                        <input type="email" name="email" required className='input min-text round-05 pd-1' placeholder={`${t("foot_email")}`} value={emailValue} onChange={(e) => setEmailValue(e.target.value)} />
                        <button type="submit" className='submit-btn min-text round-05 pd-05 op-08'>{t("send")}</button>
                    </form>
                </div>
                <div className="bottom gap-2">
                    <div className="tool gap-1 c1">
                        <p className="text link-title">{t("foot1")}</p>
                        <Link to="/courses/1" className='text link'>{t("foot2")}</Link>
                        <Link to="/courses/1" className='text link'>{t("foot3")}</Link>
                        <Link to="/free-courses/4" className='text link'>{t("foot5")}</Link>
                        <Link to="/free-courses/1" className='text link'>{t("foot4")}</Link>
                    </div>
                    <div className="tool gap-1 c2">
                        <p className="text link-title">{t("foot6")}</p>
                        <Link to="/" className='text link' onClick={changeScrollComments}>{t("foot9")}</Link>
                        <Link to="/" className='text link' onClick={changeScrollTeachers}>{t("foot8")}</Link>
                        <Link to="/" className='text link' onClick={changeScrollCourses}>{t("foot7")}</Link>
                        <Link to="/" className='text link' onClick={changeScrollFaq}>{t("foot10")}</Link>
                    </div>
                    <div className="tool gap-1 c3">
                        <p className="text link-title">{t("foot11")}</p>
                        <a href='mailto:info@dargah.uz' className='text link'>info@dargah.uz</a>
                        {/* <a href='tel:+998977000024' className='text link'>+99897 700 00 24</a> */}
                        <a target={"_blank"} href='https://www.google.com/maps/place/C-Space+Labzak/@41.3318954,69.2615942,17z/data=!3m1!4b1!4m6!3m5!1s0x38ae8b7ec64d4dbd:0x6a26177af9d218c3!8m2!3d41.3318914!4d69.2637829!16s%2Fg%2F11ng2ryb5z' className='text link'>{t("foot12")}</a>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default Footer;