import { combineReducers } from "redux";
import { changeAmountReducer, changeCourseNavigateReducer, selectTelegramUrlReducer } from "./reducer";

const reducers = combineReducers({
    selectedAmount: changeAmountReducer,
    selectedTgLink: selectTelegramUrlReducer,
    selectedNavigate: changeCourseNavigateReducer,
});

export default reducers;