import { ActionTypes } from "../actiontypes/ActionTypes"

export const changeNavigate = (item) => {
    return {
        type: ActionTypes.CHANGE_COURSE_NAVIGATE,
        payload: item
    }
};

export const changeAmount = (item) => {
    return {
        type: ActionTypes.CHANGE_AMOUNT_VALUE,
        payload: item
    }
};

export const changeTelegramUrl = (item) => {
    return {
        type: ActionTypes.CHANGE_TELEGRAM_URL,
        payload: item
    }
};