import React, { useState } from 'react';
import { useSelector } from 'react-redux';
import '../../styles/modals/FormModal.scss';
import { useTranslation } from 'react-i18next';
import { useNavigate } from 'react-router-dom';

const FormModal = ({ changeFormModal }) => {

    // redux options

    const navigate = useNavigate();
    let selectedNavigate = useSelector((state) => state.selectedNavigate.navigate);

    // form options

    const [jinsi, setJinsi] = useState("male");
    const [firstName, setFirstName] = useState("");
    const [phoneValue, setPhoneValue] = useState("");
    const [isLoading, setIsloading] = useState(false);

    function formatPhone(value) {
        if (!value) return value
        const phoneNumber = value.replace(/[^\d]/g, "");
        const phoneNumberLength = phoneNumber.length;
        if (phoneNumberLength < 3) return phoneNumber;
        if (phoneNumberLength < 6) {
            return `(${phoneNumber.slice(0, 2)}) ${phoneNumber.slice(2)}`
        }
        else if (phoneNumberLength < 8) {
            return `(${phoneNumber.slice(0, 2)}) ${phoneNumber.slice(2, 5)}-${phoneNumber.slice(5)}`
        }
        else if (phoneNumberLength < 8) {
            return `(${phoneNumber.slice(0, 2)}) ${phoneNumber.slice(2, 5)}-${phoneNumber.slice(5, 7)}-${phoneNumber.slice(7)}`
        }
        return `(${phoneNumber.slice(0, 2)}) ${phoneNumber.slice(2, 5)}-${phoneNumber.slice(5, 7)}-${phoneNumber.slice(7, 9)}`
    };

    function changePhone(e) {
        let formattedPhone = formatPhone(e.target.value);
        setPhoneValue(formattedPhone);
    };

    let bot = {
        TOKEN: "5886103475:AAGeKpLQbe4cSoda09-pXDfGObdVgrnhlbs",
        chatID: "-1001551810849",
        message: `%0AFIO: ${firstName}; %0ATelefon raqami: 998${phoneValue.replace(/[^\d]/g, "")}; %0AJinsi: ${jinsi == "male" ? "Erkak" : "Ayol"}`
    };

    function sendMessage(e) {
        e.preventDefault();
        setIsloading(true);
        fetch(`https://api.telegram.org/bot${bot.TOKEN}/sendMessage?chat_id=${bot.chatID}&text=${bot.message} `, {
            method: "GET"
        })
            .then(success => {
                changeFormModal();
                setIsloading(false);
                console.log('send message');
                navigate(`/payment/${selectedNavigate}`);
                sessionStorage.setItem('user-jinsi', jinsi);
                sessionStorage.setItem('user-name', firstName);
                sessionStorage.setItem('user-phone', `998${phoneValue.replace(/[^\d]/g, "")}`);
            }, error => {
                console.log(error)
                setIsloading(false);
            })
    };

    // i18next

    const { t } = useTranslation();

    return (
        <div className='FormModal'>
            <form onSubmit={sendMessage} className="child round-1 pd-2 gap-1">
                <h1 className="name mbot-1">{t("con_title")}</h1>
                <label htmlFor="firstname" className='label text mtop-05'>{t('fio')}</label>
                <input type="text" id='firstname' className='input text round-05 pd-07' required value={firstName} onChange={(e) => setFirstName(e.target.value)} />
                <label htmlFor="phone" className='label text mtop-05'>{t("email1")}</label>
                <div className="phone-num">
                    <div className="tel-code text">+998</div>
                    <input type="text" id='phone' required min={9} minLength={9} className="input text round-05 pd-07" placeholder="-- --- -- --" value={phoneValue} onChange={(e) => changePhone(e)} />
                </div>
                <label htmlFor="firstname" className='label text mtop-05'>{t('jins')}</label>
                <div className="jins gap-05">
                    <input type="radio" name="jins" id="male" className='radio' checked={jinsi == 'male'} onChange={() => setJinsi("male")} />
                    <label className='text' htmlFor="male">{t('male')}</label>
                </div>
                <div className="jins gap-05">
                    <input type="radio" name="jins" id="female" className='radio' checked={jinsi == 'female'} onChange={() => setJinsi("female")} />
                    <label className='text' htmlFor="female">{t('female')}</label>
                </div>
                <button type="submit" className={`pay-btn text round-05 pd-07 mtop-1-5 ${isLoading ? "loading-btn" : "op-08"}`} disabled={isLoading}>{t("send")}</button>
            </form>
            <div className="contrast-05" onClick={changeFormModal}></div>
        </div>
    );
};

export default FormModal;