import React, { useState } from 'react';
import '../../styles/home/Contacts.scss';
import { useTranslation } from 'react-i18next';

const Contacts = ({ changeModal }) => {

    // i18next

    const { t } = useTranslation();

    // form options

    const [textValue, setTextValue] = useState("");
    const [nameValue, setNameValue] = useState("");
    const [numberValue, setNumberValue] = useState("");

    let bot = {
        TOKEN: "5886103475:AAGeKpLQbe4cSoda09-pXDfGObdVgrnhlbs",
        chatID: "-1001551810849",
        message: `%0AIsmi: ${nameValue}; %0ATelefon raqami: ${numberValue}; %0AXabar: ${textValue}`
    };

    function sendMessage(e) {
        e.preventDefault();
        fetch(`https://api.telegram.org/bot${bot.TOKEN}/sendMessage?chat_id=${bot.chatID}&text=${bot.message} `, {
            method: "GET"
        })
            .then(success => {
                console.log('send message')
            }, error => {
                console.log(error)
            })

        changeModal();
        setNameValue("");
        setTextValue("");
        setNumberValue("");
    };

    return (
        <div className='Contacts parent' id='contacts'>
            <div className="wrapper gap-1">
                <div className="left gap-1">
                    <h1 className="sub-title">{t("contact_title")}</h1>
                    <p className="text">{t("contact_text")}</p>
                </div>
                <form onSubmit={sendMessage} className="right gap-1">
                    <div className="inputs gap-1">
                        <div className="tool">
                            <label htmlFor="name" className='min-text'>{t("name")}</label>
                            <input type="text" id="name" className="form-input min-text round-07" required minLength={3} min={3} value={nameValue} onChange={(e) => setNameValue(e.target.value)} placeholder={`${t("name1")}`} />
                        </div>
                        <div className="tool">
                            <label htmlFor="number" className='min-text'>{t("email")}</label>
                            <input type="number" id="number" required minLength={3} min={3} className="form-input min-text round-07" value={numberValue} onChange={(e) => setNumberValue(e.target.value)} placeholder={`${t("email1")}`} />
                        </div>
                    </div>
                    <div className="textarea">
                        <label htmlFor="message" className='min-text'>{t("message")}</label>
                        <textarea id="message" rows="5" className='form-input min-text round-07' value={textValue} onChange={(e) => setTextValue(e.target.value)} placeholder={`${t("message1")}`}></textarea>
                    </div>
                    <button type='submit' className='send-btn min-text scale-05 round-05'>{t("send")}</button>
                </form>
            </div>
        </div>
    );
};

export default Contacts;