import React from 'react';
import Footer from '../components/Footer';
import Navbar from '../components/Navbar';
import { Outlet } from 'react-router-dom';
import Modal from '../components/modals/Modal';
import ScrollToTop from '../components/ScrollToTop';
import FormModal from '../components/modals/FormModal';

const RootLayout = ({ showModal, changeModal, showFormModal, changeFormModal, changeScrollFaq, changeScrollComments, changeScrollAdvantage, changeScrollTeachers, changeScrollCourses }) => {
    return (
        <div>
            <ScrollToTop />
            <Navbar changeScrollFaq={changeScrollFaq} changeScrollComments={changeScrollComments} changeScrollAdvantage={changeScrollAdvantage} changeScrollTeachers={changeScrollTeachers} changeScrollCourses={changeScrollCourses} />
            {showModal &&
                <Modal changeModal={changeModal} />
            }
            {showFormModal &&
                <FormModal changeFormModal={changeFormModal} />
            }
            <Outlet />
            <Footer changeModal={changeModal} changeScrollFaq={changeScrollFaq} changeScrollComments={changeScrollComments} changeScrollTeachers={changeScrollTeachers} changeScrollCourses={changeScrollCourses} />
        </div>
    );
};

export default RootLayout;