import { ActionTypes } from "../actiontypes/ActionTypes";

const initialState = {
    dataNavigate: { navigate: "" },
    dataTelegramUrl: { tgLink: "#" },
    dataAmountValue: { amount: "0" },
};

export const changeCourseNavigateReducer = (state = initialState.dataNavigate, { type, payload }) => {
    switch (type) {
        case ActionTypes.CHANGE_COURSE_NAVIGATE:
            return { navigate: payload }
        default:
            return state
    }
};

export const changeAmountReducer = (state = initialState.dataAmountValue, { type, payload }) => {
    switch (type) {
        case ActionTypes.CHANGE_AMOUNT_VALUE:
            return { amount: payload }
        default:
            return state
    }
};

export const selectTelegramUrlReducer = (state = initialState.dataTelegramUrl, { type, payload }) => {
    switch (type) {
        case ActionTypes.CHANGE_TELEGRAM_URL:
            return { tgLink: payload }
        default:
            return state
    }
};