import React, { useState } from 'react';
import '../styles/Navbar.scss';
import { Link } from 'react-router-dom';
import Logo from '../assets/images/logo2.webp';
import { useTranslation } from 'react-i18next';
import { changeLang } from '../languages/language';
import { AngleDownIcon, CloseIcon, MenuIcon } from '../assets/svgIcons';

const Navbar = ({ changeScrollFaq, changeScrollComments, changeScrollAdvantage, changeScrollTeachers, changeScrollCourses }) => {

    // languages options

    const { i18n, t } = useTranslation();
    let lang = localStorage.getItem('i18nextLng');
    const [language, setLanguage] = useState(false);

    function changeLangValue(item) {
        changeLang(item);
        setLanguage(false);
        i18n.changeLanguage(item);
    };

    // responsive options

    const [showMenu, setShowMenu] = useState(false);

    function changeShowMenu() {
        setShowMenu(!showMenu)
    };

    return (
        <div className='Navbar'>
            <div className="top-navbar">
                <div className="welcome min-text">{t("welcome")}</div>
                <div className="tools gap-1-5">
                    <a href="https://www.google.com/maps/place/C-Space+Labzak/@41.3318954,69.2615942,17z/data=!3m1!4b1!4m6!3m5!1s0x38ae8b7ec64d4dbd:0x6a26177af9d218c3!8m2!3d41.3318914!4d69.2637829!16s%2Fg%2F11ng2ryb5z" target={"_blank"} className='tool gap-05 min-text'>{t("location")}</a>
                    <a href="#contacts" className='tool min-text gap-05'>{t("contact")}</a>
                    {/* <a href="tel:+99897-700-00-24" className='tool-btn min-text round-05 scale-05'>{t("tel")}</a> */}
                    {/* <div className="language">
                        <div className="lang-title tool-btn min-text round-05 scale-05" onClick={() => setLanguage(!language)}>
                            {lang == "uz" ? "O'Z" : "ЎЗ"}
                            <div className="down-icon"><AngleDownIcon /></div>
                        </div>
                        {language &&
                            <div className="lang-menu round-05">
                                <div className="lang min-text" onClick={() => changeLangValue("uz")}>O'Z</div>
                                <div className="lang min-text" onClick={() => changeLangValue("ru")}>ЎЗ</div>
                                <div className="lang min-text" onClick={() => changeLangValue("ru")}>RU</div>
                            </div>
                        }
                        {language &&
                            <div className="contrast-0" onClick={() => setLanguage(!language)}></div>
                        }
                    </div> */}
                </div>
            </div>
            <div className="bottom-nabar gap-2">
                <Link to="/" onClick={() => window.scrollTo(0, 0)} className="logo">
                    <img src={Logo} alt="logo" className="logo-img" />
                </Link>
                <div className={`links gap-2 ${showMenu && "show-links"}`}>
                    <Link to="/" className="link text" onClick={() => [changeScrollCourses(), changeShowMenu()]}>{t("nav1")}</Link>
                    <Link to="/" className="link text" onClick={() => [changeScrollAdvantage(), changeShowMenu()]}>{t("nav2")}</Link>
                    <Link to="/" className="link text" onClick={() => [changeScrollTeachers(), changeShowMenu()]}>{t("nav3")}</Link>
                    <Link to="/" className="link text" onClick={() => [changeScrollComments(), changeShowMenu()]}>{t("nav4")}</Link>
                    <Link to="/" className="link text" onClick={() => [changeScrollFaq(), changeShowMenu()]}>{t("nav5")}</Link>
                    <div className="close-icon" onClick={changeShowMenu}><CloseIcon /></div>
                </div>
                <div className="menu-icon" onClick={changeShowMenu}><MenuIcon /></div>
            </div>
        </div>
    );
};

export default Navbar;