import React from 'react';
import "swiper/css/navigation";
import "swiper/css/pagination";
import { Link } from 'react-router-dom';
import '../../styles/home/HomeCourses.scss';
import { dataCourses } from '../../api/Api';
import { useTranslation } from 'react-i18next';
import { Navigation, Pagination } from "swiper";
import { BoltIcon } from '../../assets/svgIcons';
import { Swiper, SwiperSlide } from "swiper/react";
import { getName } from '../../languages/language';

const HomeCourses = () => {

    let lang = localStorage.getItem('i18nextLng');

    // i18next

    const { t } = useTranslation();

    return (
        <div className='HomeCourses parent'>
            <div className="wrapper gap-2">
                <h1 className="sub-title">{t('course_title')}</h1>
                <Swiper navigation={true} spaceBetween={20} pagination={{ clickable: true }} loop={true} modules={[Navigation, Pagination]} className="mySwiper">
                    {dataCourses.map((item) => (
                        <SwiperSlide key={item.id} className="course pd-3 round-1">
                            <div className="infos gap-1-5">
                                <div className="top gap-1">
                                    <p className="min-text">{t('science')}: {lang == "uz" ? item.science_lat : item.science_kril}</p>
                                    {!item.disabled &&
                                        <div className="stamp min-text">
                                            <div className="icon"><BoltIcon /></div>
                                            {t('stamp')}
                                        </div>
                                    }
                                </div>
                                <div className="middle gap-05">
                                    <h1 className="name">{getName(item)}</h1>
                                    {/* <p className="min-text">{lang == "uz" ? item.desc_lat : item.desc_kril}</p> */}
                                    <p className="min-text">{lang == "uz" ? item.description_lat : item.description_kril}</p>
                                </div>
                                <div className="bottom gap-1 mtop-05">
                                    {!item.disabled &&
                                        <Link to={`/courses/${item.id}`} className='explore min-text round-05 scale-05'>{t('pay_btn1')}</Link>
                                    }
                                    {!item.disabled &&
                                        <Link to={`/courses/${item.id}`} className='explore min-text round-05 scale-05'>{t('course_btn1')}</Link>
                                    }
                                </div>
                            </div>
                            <img src={item.image1} alt="img" className="img" />
                        </SwiperSlide>
                    ))}
                </Swiper>
            </div>
        </div>
    );
};

export default HomeCourses;