import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { useTranslation } from 'react-i18next';
import { useLocation } from 'react-router-dom';
import '../../styles/payments/Congratulations.scss';

const Congratulations = () => {

    // router options

    const path = useLocation();

    // page options

    const [showPage, setShowPage] = useState(false);

    useEffect(() => {
        setTimeout(() => {
            setShowPage(true);
        }, 3000);
    }, [])

    // change window options after user paid

    const [getPaid, setGetPaid] = useState(false);
    let token = localStorage.getItem("course-token");

    function changeWindow() {
        const payData = new FormData();
        payData.append('token', token);

        axios.post(`https://api.dargah.uz/backend/transaction/check`, payData)
            .then(function (response) {
                setGetPaid(response.data.error);
                if (!response.data.error) {
                    window.open(response.data.invite_link);
                }
            })
            .catch(function (error) {
                console.log(error);
            });
    };

    // i18next

    const { t } = useTranslation();
    let lang = localStorage.getItem('i18nextLng');

    return (
        <div className='Congratulations parent'>
            {getPaid ?
                <div className="wrapper gap-3">
                    {lang == 'uz' ?
                        <h1 className="title mulish-700 t-center">Uzur! To'lovni amalga oshirishda qandaydir xatolik yuz bergan ko'rinadi. Muammoni bartaraf etish uchun quyidagi aloqa kontaktlari bilan bog'laning:</h1>
                        :
                        <h1 className="title mulish-700 t-center">Узур! Тўловни амалга оширишда қандайдир хатолик юз берган кўринади. Муаммони бартараф этиш учун қуйидаги алоқа контактлари билан боғланинг:</h1>
                    }
                    {/* <a href='tel:+998977000024' className="sub-title mulish-400 t-center">+99897 700 00 24</a> */}
                    <a href='mailto:info@dargah.uz' className="sub-title mulish-400 t-center">info@dargah.uz</a>
                </div>
                :
                <div className="wrapper gap-3">
                    {showPage ?
                        <div className="any gap-3">
                            {/* <h1 className="title mulish-700 t-center">{t("congrate1")}</h1> */}
                            {/* <h1 className="sub-title mulish-400 t-center">{t("congrate2")}</h1> */}
                            {lang == 'uz' ?
                                <h1 className="title mulish-700 t-center">To'lovni amalga oshirganingizni tekshiring va maxsus telegram guruhga qo'shiling!</h1>
                                :
                                <h1 className="title mulish-700 t-center">Тўловни амалга оширганингизни текширинг ва махсус телеграм гуруҳга қўшилинг!</h1>
                            }
                            <div className="btns mtop-1">
                                {/* <button className="explore-bg glass-anim scale-05 round-05 big-text pd-1" onClick={changeWindow}>{t("congrate3")}</button> */}
                                {lang == 'uz' ?
                                    <button className="explore-bg glass-anim scale-05 round-05 big-text pd-1" onClick={changeWindow}>To'lovni tekshirish</button>
                                    :
                                    <button className="explore-bg glass-anim scale-05 round-05 big-text pd-1" onClick={changeWindow}>Тўловни текшириш</button>
                                }
                            </div>
                        </div>
                        :
                        <h1 className="title">LOADING...</h1>
                    }
                </div>
            }
        </div>
    );
};

export default Congratulations;