import React, { useState } from 'react';
import Contacts from '../home/Contacts';
import { useTranslation } from 'react-i18next';
import { dataFreeCourses } from '../../api/Api';
import { Link, useParams } from 'react-router-dom';
import '../../styles/freecourses/FreeCourses.scss';
import { getDescription, getLink, getName } from '../../languages/language';

const FreeCourses = () => {

    // router options

    const { id } = useParams();

    // video play options

    const [autoPlayNum, setAutoPlayNum] = useState(0);
    const [videoDisable, setVideoDisable] = useState(false);

    function changeDisable() {
        setAutoPlayNum(1)
        setTimeout(() => {
            setVideoDisable(true)
        }, 420000);
    };

    // form options

    const [nameValue, setNameValue] = useState("");
    const [showVideo, setShowVideo] = useState(false);
    const [numberValue, setNumberValue] = useState("");

    let bot = {
        TOKEN: "5886103475:AAGeKpLQbe4cSoda09-pXDfGObdVgrnhlbs",
        chatID: "-1001551810849",
        message: `%0AIsmi: ${nameValue}; %0ATelefon raqami: ${numberValue}`
    };

    function sendMessage(e) {
        e.preventDefault();

        fetch(`https://api.telegram.org/bot${bot.TOKEN}/sendMessage?chat_id=${bot.chatID}&text=${bot.message} `, {
            method: "GET"
        })
            .then(success => {
                setNameValue("");
                setNumberValue("");
                setShowVideo(true);
                console.log('send message')
            }, error => {
                console.log(error)
            })
    };

    // i18next

    const { t } = useTranslation();
    let lang = localStorage.getItem('i18nextLng');

    return (
        <>
            <div className='FreeCourses parent'>
                {showVideo ?
                    dataFreeCourses.filter((c) => c.id == id).map((item) => (
                        <div key={item.id} className="wrapper gap-2-5">
                            <div className="texts gap-1-5">
                                <h1 className="big-text t-center">{t("free")}</h1>
                                <h1 className="title mulish-700 t-center">{getName(item)}</h1>
                                <p className="big-text t-center">{getDescription(item)}</p>
                            </div>
                            <div className="main-video">
                                {autoPlayNum == 0 &&
                                    <div className="contr" onClick={changeDisable}></div>
                                }
                                <iframe className='round-1-5' src={`https://www.youtube.com/embed/${item.video}?autoplay=${autoPlayNum}&cc_load_policy=1`} title="YouTube video player" frameBorder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share" allowFullScreen></iframe>
                            </div>
                            <div className="btns">
                                {videoDisable &&
                                    <Link to={`/courses/${item.url_id}`} className="explore-bg scale-05 big-text round-05 pd-1-5">{getLink(item)}</Link>
                                }
                            </div>
                        </div>
                    ))
                    :
                    dataFreeCourses.filter((c) => c.id == id).map((item) => (
                        <div key={item.id} className="wrapper">
                            {lang == "uz" ?
                                <h1 className="sub-title texts t-center">Ma'lumotlaringizni to'ldiring va bepul darslikni qo'lga kiriting</h1>
                                :
                                <h1 className="sub-title texts t-center">Маълумотларингизни тўлдиринг ва бепул дарсликни қўлга киритинг</h1>
                            }
                            <form onSubmit={sendMessage} className="forms texts mtop-3 gap-1">
                                <input type="text" className='input text pd-1-5 round-05' required placeholder={`${t("name1")}`} value={nameValue} onChange={(e) => setNameValue(e.target.value)} />
                                <input type="number" className='input text pd-1-5 round-05' required placeholder={`${t("phone")}`} value={numberValue} onChange={(e) => setNumberValue(e.target.value)} />
                                <button type='submit' className='send-btn big-text mtop-1 scale-05 round-07 pd-1'>{t("send")}</button>
                            </form>
                        </div>
                    ))
                }
            </div>
            <Contacts />
        </>
    );
};

export default FreeCourses;